export enum ActivityType {
  apk = 0,
  prom = 1
}

export class ActivityTypeMapper {
  public static getLabel(type: ActivityType): '' | '' {
    switch (+type) {
      case ActivityType.apk:
        return '';
        break;

      case ActivityType.prom:
        return '';
        break;
    }

    return undefined;
  }
}
